.programs-table-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: calc(100vh - 272px);
  overflow: hidden;

  .table-scroll-container {
    height: calc(100vh - 290px);
    overflow-y: scroll;
  }
}

.programs-table-skeleton {
  width: 100%;
  height: 440px;
  overflow: hidden;
  // background-color: aqua;

  .row-skeleton {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 14px;
    height: 48px;
    // margin-top: 8px;
    border-bottom-width: 1px;
    border-bottom-style: solid;

    div {
      // display: inline-block;
      border-radius: 16px;
      min-width: 193px;
      height: 30px;
      overflow: hidden;
      position: relative;
      width: 100%;
      // margin-bottom: 4px;
      // padding: 4px;

      &::after {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        transform: translateX(-100%);
        animation: shimmer 1s infinite;
        content: '';
      }

      @keyframes shimmer {
        100% {
          transform: translateX(100%);
        }
      }
    }

    &:last-of-type {
      border-bottom: 0;
    }
  }
}
